.footer{position:relative;display:flex;flex-direction:column;align-items:center;height:591px}.footer_bg{position:absolute;width:100%;height:100%;background-image:url(../img/BG_futer.png);z-index:-1}.footer_reserved{font-family:"Montserrat",sans-serif;font-size:14px;font-weight:400;color:#fff !important}.footer-logo{width:153px;margin:0 0 65px}.soсial-case{display:flex;justify-content:center;align-items:center;width:100%;margin:0 0 50px;cursor:pointer}.soсial-case>a{margin:0 27px;transition:.2s}.soсial-case>a:hover{transform:scale(1.3)}.footer-case{display:flex;justify-content:space-around;width:100%;max-width:1284px;margin:91px 0 0}.footer-case_nav{display:flex;flex-direction:column;justify-content:space-between;width:141px;height:190px}.footer-case_nav>button{background:rgba(0,0,0,0);padding:0;font-family:"Montserrat",sans-serif;font-size:16px;font-weight:700;color:#fff;text-align:start;border:0;transition:.1s}.footer-case_nav>button:hover{transform:scale(1.3) translateX(10px)}.footer-case_center{display:flex;flex-direction:column;align-items:center;margin:25px 0 0}.footer-case>a{height:49px}